<template>
    <!--   -->
    <div id="modal" class="modal" v-if="visible">

        <!--   -->
        <div class="first-modal-content">

            <!--   -->
            <!--      -->
            <div class="modal-title" v-if="firstPage">
                <h2>GS MMS VS </h2>
                <div>
                      | 2024.10.24() 19:00
                </div>
                <hr class="divider" />
            </div>


            <!--   -->
            <div v-if="firstPage">

                <div class="modal-body">
                    <!--   -->
                    <div class="modal-ticket-img" v-if="selectedZoneImage">
                        <img :src="selectedZoneImage" alt="  " class="stadium-image" />
                    </div>

                    <!--   -->
                    <div class="modal-table-container">
                        <table class="modal-table">
                            <thead class="table-theader">
                                <tr>
                                    <th></th>
                                    <th></th>
                                    <th></th>
                                </tr>
                            </thead>

                            <!-- DB     -->
                            <tbody>
                                <tr>
                                    <td>
                                        <input type="checkbox" class="select-zone-btn" v-model="isChecked"
                                            @change="onCheckboxChange">
                                         [GOLD ZONE]
                                    </td>
                                    <td>
                                    </td>
                                    <td>90</td>
                                </tr>
                            </tbody>
                        </table>

                        <!--   -->
                        <table v-if="isChecked" class="modal-table-hide">
                            <tbody>

                                <tr v-for="(zone, index) in zones" :key="zone.name">
                                    <td>{{ zone.name }}</td>
                                    <td>
                                        <div class="quantity-selector">
                                            <button @click="decreaseQuantity(index)"
                                                :disabled="zone.quantity <= 0 || (activeButtonIndex !== null && activeButtonIndex !== index)">
                                                -
                                            </button>
                                            <input type="text" v-model="zone.quantity" readonly class="quantity-box" />
                                            <button @click="increaseQuantity(index)"
                                                :disabled="zone.quantity >= zone.maxSeats || (activeButtonIndex !== null && activeButtonIndex !== index)">
                                                +
                                            </button>
                                        </div>
                                    </td>
                                    <td>{{ zone.fullSeats }}</td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
                <hr class="divider" />

                <div class="result-text">
                        .
                    <div v-for="zone in selectedZones" :key="zone.name">
                          : {{ zone.name }} {{ zone.quantity }}
                    </div>
                    <div class="result-text-all">
                         : {{ countTicket.count }}
                    </div>
                </div>
                <div class="first-button">
                    <!--   -->
                    <button class="buy-button" @click="openSecondPage"></button>

                    <!--   -->
                    <button class="close-button" @click="closeModal"></button>
                </div>

            </div>
            <!-- ------------------------------------------------------------------ -->

            <!-- 2   -->
            <div v-if="secondPage" class="second-modal-content">
                <div class="modal-body">

                    <!--   -->
                    <div class="left-table">
                        <div class="modal-table-container">
                            <table class="modal-table">
                                <thead class="table-theader">
                                    <tr>
                                        <th></th>
                                        <th></th>
                                        <th></th>
                                        <th></th>
                                    </tr>
                                </thead>

                                <!-- DB     -->
                                <tbody>
                                    <tr v-for="zone in selectedZones" :key="zone.name">
                                        <td> {{ zone.name }} </td>
                                        <td> {{ zone.quantity }}</td>
                                        <td> {{ zone.price }}</td>
                                        <td></td>
                                    </tr>
                                </tbody>
                            </table>

                            <table class="modal-table">
                                <thead class="table-theader">
                                    <tr>
                                        <td colspan="4"></td>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr>
                                        <td colspan="4">{{ formattedTotal }}</td>
                                    </tr>
                                </tbody>
                            </table>

                            <div class="modal-info-box">
                                <div class="icon">
                                    <img src="@/assets/img/anyImg/bell-icon.png" alt="alert" />
                                </div>
                                <div class="info-content">
                                    <h4> </h4>
                                    <ul>
                                        <li> -     1  </li>
                                        <li> -           .</li>
                                    </ul>
                                    <h4> </h4>
                                    <p class="p">   12  
                                    </p>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div class="vertical-divider"></div>

                    <!--   -->
                    <div class="right-table">

                        <!--   -->
                        <!--      -->
                        <div class="modal-second-title">
                            <h2>GS MMS VS </h2>
                            <div>
                                  | 2024.10.24() 19:00
                            </div>
                        </div>
                        <div>
                            
                        </div>
                        <div v-for="zone in selectedZones" :key="zone.name">
                            {{ zone.name }} / {{ zone.quantity }}
                        </div>
                        <hr class="divider" />

                        <div>
                            <table class="second-modal-table">
                                <tr>
                                    <td> </td>
                                    <td>
                                        {{ formattedTotal }}
                                    </td>
                                </tr>
                                <tr>
                                    <td> </td>
                                    <td>
                                        
                                    </td>
                                </tr>
                                <tr>
                                    <td>  </td>
                                    <td>
                                        {{ formattedTotal }}
                                    </td>
                                </tr>
                            </table>
                            <br>
                            <table class="second-modal-table">
                                <tr>
                                    <td> </td>
                                    <td> </td>
                                </tr>
                                <tr>
                                    <td> -   </td>
                                    <td>  </td>
                                </tr>
                                <tr>
                                    <td> </td>
                                    <td> </td>
                                </tr>
                            </table>

                        </div>
                        <div>
                            <p class="p">
                                 :  [12 ]<br>
                                <!--  -->
                                 : []
                            </p>
                        </div>
                        <div class="second-button">
                            <!--   -->
                            <button class="buy-button" @click="openThirdPage"> </button>
                            <!--   -->
                            <button class="close-button" @click="openFirstPage"></button>
                        </div>
                    </div>


                </div>
            </div>
        </div>
    </div>
</template>

<script>

export default {
    name: 'TicketModal',

    components: {

    },

    props: {
        visible: {
            type: Boolean,
            default: false,
        },
    },

    computed: {
        selectedZones() {
            //  1   
            return this.zones.filter(zone => zone.quantity > 0);
        },

        formattedTotal() {
            //       
            return this.total.toLocaleString();
        },

    },

    watch: {
        //    ,   0 activeButtonIndex 
        zones: {
            handler() {
                if (this.zones.every(zone => zone.quantity === 0)) {
                    this.activeButtonIndex = null;
                }
            },
            deep: true
        }
    },

    data() {
        return {

            //  
            zones: [
                { name: 'GA', quantity: 0, maxSeats: 4, fullSeats: '30', imageUrl: 'stadium-GA.jpg', price: 30000 },
                { name: 'GB', quantity: 0, maxSeats: 4, fullSeats: '30', imageUrl: 'stadium-GB.jpg', price: 30000 },
                { name: 'GC', quantity: 0, maxSeats: 4, fullSeats: '30', imageUrl: 'stadium-GC.jpg', price: 30000 }
            ],

            activeButtonIndex: null, //     

            isChecked: false, //   

            //  
            countTicket: {
                count: 0,
            },

            //    URL
            selectedZoneImage:
                require('@/assets/img/stadium/stadium-main.jpg'),

            //   
            firstPage: true,
            secondPage: false,

            total: 0,

        };
    },

    methods: {
        // 
        calculateTotal() {
            for (let i = 0; i < this.selectedZones.length; i++) {
                this.total = this.selectedZones[i].price * this.selectedZones[i].quantity + this.total;
            }
        },

        //   
        openFirstPage() {
            this.firstPage = true;
            this.secondPage = false;
            this.total = 0;
        },

        //   
        openSecondPage() {
            this.calculateTotal();
            if (this.total > 0) {
                this.firstPage = false;
                this.secondPage = true;
            } else {
                alert(" .");
            }
        },


        onCheckboxChange() {
            if (this.isChecked) {
                this.showDetails();
            } else {
                this.hideDetails();
            }
        },
        showDetails() {
        },
        hideDetails() {
            this.countTicket.count = 0;
            this.zones.forEach(zone => {
                zone.quantity = 0; //    0                 
            });
            this.selectedZoneImage =
                require('@/assets/img/stadium/stadium-main.jpg'); //  
        },

        //   
        closeModal() {
            this.hideDetails();
            this.isChecked = false;
            this.$emit('close');
        },
        openModal() {
            this.isModalOpen = true;
        },
        //  
        increaseQuantity(index) {
            if (this.zones[index].quantity < this.zones[index].maxSeats) {
                this.zones[index].quantity++;
                this.activeButtonIndex = index; //     
                this.countTicket.count++;
                this.selectedZoneImage = require(`@/assets/img/stadium/${this.zones[index].imageUrl}`);

            }
        },
        decreaseQuantity(index) {
            if (this.zones[index].quantity > 0) {
                this.zones[index].quantity--;
                this.activeButtonIndex = index; //     
                this.countTicket.count--;
                this.selectedZoneImage = require(`@/assets/img/stadium/${this.zones[index].imageUrl}`);

            }
        },

        selectZone(index) {
            //    URL 
            this.selectedZoneImage = this.zones[index].imageUrl;
        },

    }
}
</script>


