<template>
    <div class="membership-purchase-page">
      <!--  1      -->
      <LogoHeader />
  
      <!--  ,    -->
      <div class="menu">
        <div class="menu-item" :class="{ 'active-menu-item': activeMenu === '/membership/info' }" @click="navigateTo('/membership/info')"> </div>
        <div class="menu-item" :class="{ 'active-menu-item': activeMenu === '/membership/purchase' }" @click="navigateTo('/membership/purchase')"> </div>
      </div>
    
      <!--     -->
      <div class="membership-purchase-content">
        <h4>2024/25  </h4>
        <div class="membership-info-layout">
          <table class="membership-info-table">
            <thead>
              <tr>
                <th></th>
                <th></th>
                <th> </th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>GOLD</td>
                <td>
                    <p>  </p>
                    <p> 7 </p>
                    <p>   ()</p>
                    <p> (MMS , ) </p>
                </td>
                <td>300,000</td>
              </tr>
              <tr>
                <td>SILVER</td>
                <td>
                    <p> 7 </p>
                    <p> (MMS , ) </p>
                </td>
                <td>100,000</td>
              </tr>
            </tbody>
          </table>
        </div>
        
          <!--   -->
          <div class="membership-selection">
            <p><strong> </strong></p>
            <select class="select-form" v-model="selectedMembershipPrice" @change="updatePrices">
              <option value=""></option>
              <option value="100000">SILVER</option>
              <option value="300000">GOLD</option>
            </select>
          </div>

        <div class="charge">
          <table class="charge-table">
            <tr>
              <th> </th>
              <th> </th>
              <th></th>
            </tr>
            <tr>
              <td>{{ formattedPayment }} </td>
              <td>- {{ formattedMembershipPrice }} </td>
              <td>{{ formattedLeftMoney }} </td>
            </tr>
          </table>
          <button class="btn" id="btn-charge" v-if="leftMoney < 0" onclick="window.location.href='http://localhost:8080//myPage/info/recharge';"></button>
        </div>

        <div class="buttons">
          <button class="btn" id="btn-purchase" @click="purchaseMembership"></button>
          <button class="btn" id="btn-cancel" onclick="window.location.href='http://localhost:8080/membership/info';"></button>
        </div>
      </div>
    </div>
  </template>
  
  <script>
  import LogoHeader from '../common/LogoHeader.vue';
  
  export default {
      name: 'MembershipPurchase',
      components: {
        LogoHeader
    },    
    data() {
      return {
        activeMenu: this.$route.path, //   
        selectedMembershipPrice: 0,   //   
        payment: 0,                   //   
        membershipData: {},           // API   
      };
    },
    watch: {
      //    activeMenu 
      $route(to) {
        this.activeMenu = to.path;
      }
    },
    computed: {
      formattedPayment() {
        return this.payment.toLocaleString();
      },
      formattedMembershipPrice() {
        return this.selectedMembershipPrice.toLocaleString();
      },
      leftMoney() {
        return this.payment - this.selectedMembershipPrice;
      },
      formattedLeftMoney() {
        return this.leftMoney.toLocaleString();
      },
    },
    methods: {
      navigateTo(route) {
        this.$router.push(route);
        this.activeMenu = route; //      
      },
      //  API    
      fetchUserMoney() {
        fetch("/membership/purchase")
          .then(response => response.json())
          .then(data => {
            this.payment = data.userMoney;
            this.membershipData = data.newMembership; // API  newMembership  
            this.updatePrices();
          })
          .catch(error => console.error('Error fetching user money:', error));
      },
      //      
      updatePrices() {
        // leftMoney , computed  
      },
      //      
      purchaseMembership() {
        //    
        let membershipId = 4;
            if (this.selectedMembershipPrice === 100000) {
              membershipId = 5;
            } else if (this.selectedMembershipPrice === 300000) {
              membershipId = 6;
            }
            // newMembership membershipSalesMembershipId  membershipId 
            this.membershipData.membershipSalesMembershipId = membershipId;

            fetch("/membership/purchase/completed", {
              method: "POST",
              headers: {
                "Content-Type": "application/json",
              },
              body: JSON.stringify(this.membershipData),
            })
              .then(response => response.json())
              .then(data => console.log(data))
              .catch(error => console.error('Error purchasing membership:', error));
          },
        },
        mounted() {
          this.fetchUserMoney(); //      
        },
  };
  </script>
  
  <style>
  .membership-purchase-page {
      padding-top: var(--header-height);
      padding-bottom: var(--footer-height);
      text-align: center;
  }
  
  /*  */
  .menu {
    display: flex;
    justify-content: center;
    gap: 50px;
    padding-bottom: 20px;
  }
  
  .menu-item {
    width: 120px;
    padding: 10px 0;
    position: relative;
    cursor: pointer;
    font-weight: bold;
    color: #565656;
  }
  
  .menu-item::after {
    content: "";
    position: absolute;
    bottom: -5px; /*       */
    left: 0;
    width: 100%;  /*   div   */
    height: 3px;
    background-color: black;
    transform: scaleX(0); /*    */
    transition: transform 0.3s ease;
  }
  
  .active-menu-item {
    color: black;  /*     black  */
  }
  
  .active-menu-item::after {
    transform: scaleX(1);  /*     */
  }
  
  .menu-item:hover::after {
    transform: scaleX(1);  /*     */
  }
  
  /*   */

.membership-purchase-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  max-width: 90%; /*    */
  margin: 0 auto;
}

.membership-info-layout {
  width: 80%; /*   50%  */
  margin-bottom: 30px; /*    */
}

.membership-info-table {
  width: 100%; /*     */
  margin: 0 auto; /*   */
  padding: auto;
  font-size: 21;
}

.membership-selection {
  display: flex;
  margin-top: 0px;
  margin-bottom: 30px;
  gap: 30px;
  text-align: center;
  width: 40%;
  margin-left: 20px;
}

.select-form {
  width: 60%;
  text-align: center
}

.charge {
  text-align: center;
  width: 60%; /*   50%  */
}

.charge-table {
  width: 80%; /*     */
  margin: 0 auto; /*   */
  /* margin-bottom: 10px; */
}

.buttons{
  margin-top: 30px;
  margin-bottom: 50px;
  gap: 20px;
}
/*  css */
.btn {
  border: none;
  /*   */
  color: white;
  /*   () */
  padding: 8px 16px;
  /*    ( 12px,  24px) */
  text-align: center;
  /*    */
  text-decoration: none;
  /*   */
  display: inline-block;
  /*     */
  font-size: 22px;
  /*   */
  border-radius: 8px;
  /*   */
  cursor: pointer;
  /*      */
  transition: background-color 0.3s;
  /*     */
  margin-top: 50px;
  margin-left: 15px;
  margin-right: 15px;
  margin-bottom: 50px;

  width: 100px;
  /*   */
  height: 50px;
  /*   */
}

#btn-purchase {
  width: 140px;
  background-color: #4CAF50;  /*   () */
}

#btn-purchase:hover {
  background-color: #45a049; /*      */
}
#btn-charge {
  width: 140px;
  background-color: #FFBB00;
  margin-bottom: 0px;
}

#btn-charge:hover {
  background-color: #EDA900; /*      */
}

#btn-cancel {
  background-color: #A6A6A6;  /*   () */
}


  </style>