<template>
  <div class="content">
    <!--    -->
     <div class="control-box">
      <div class="user-info-box">
      <div v-if="isLoggedIn">
        <div v-if="role == 'USER'"> 
          <span class="membership-image-container">
        <span v-if="membershipLevel == 'GOLD'">
          <img :src="goldImage" alt=" " class="membershipLevel-image" />
        </span>
         <span v-else-if="membershipLevel == 'SILVER'">
           <img :src="silverImage" alt=" " class="membershipLevel-image" />
        </span>
        <span v-else>
          <img :src="bronzeImage" alt=" " class="membershipLevel-image" />
        </span>
        <p><strong>{{ name }}</strong> </p>
        </span>

        <div class="money-box">
          <p>: <strong>{{ balance }}</strong></p><button class="btn-charge" @click="goToRecharge"></button>
        </div>
          <button class="btn-myPage" @click="goToMyPage">My Page</button>&nbsp;<button class="btn-logout" @click="logout"></button>
        </div>


        <div v-else> 
          <p class="Admin-notice">**** ****</p>
          <p><strong>{{ name }}</strong> </p>
          <button class="btn-AdminPage" @click="goToAdminPage">ADMIN Page</button><br>
          <button class="btn-logout2" @click="logout"></button>
        </div>
        </div>
      
      <div v-else>
        <button @click="goToLogin" class="btn-login"></button><br>
        <button @click="goToSignup" class="btn-signup"></button>
      </div>
      </div>
    </div>
  

    <!--   -->
    <div class="main-content">
      <!--    -->
      <div class="slider-container">
        <div class="slides" :style="{ transform: `translateX(-${currentSlide * 100}%)` }">
          <div v-for="(slide, index) in slides" :key="index" class="slide">
            <img :src="slide.img" :alt="'Slide ' + (index + 1)" /> <!--   -->
          </div>
        </div>
        <button class="prev" @click="prevSlide">
          <img src="@/assets/img/common/content-slide-icon-left-arrow.png" alt="Previous Slide" />
        </button>
        <button class="next" @click="nextSlide">
          <img src="@/assets/img/common/content-slide-icon-right-arrow.png" alt="Next Slide" />
        </button>
        <div class="indicators">
          <img v-for="(slide, index) in slides" 
            :key="index" 
            :src="currentSlide === index ? require('@/assets/img/common/content-slide-icon-filled-circle.png') : require('@/assets/img/common/content-slide-icon-empty-circle.png')"
            @click="goToSlide(index)"
            alt="Slide Indicator"
            class="indicator-img" />
        </div>
      </div>

      <!--    -->
      <div class="section">
        <h2> </h2>
        <div class="highlight-slider-container">
          <div class="highlight-slides" :style="{ transform: `translateX(-${currentHighlightSlide * 100}%)` }">
            <div v-for="(slide, index) in highlightSlides" :key="index" class="highlight-slide">
              <img :src="slide.img" :alt="'Highlight Slide ' + (index + 1)" />
              <img class="highlight-play-button" src="@/assets/img/common/content-slide-icon-play-button.png" alt="Play Button" />
              <div class="highlight-overlay"></div>
            </div>
          </div>
          <button class="highlight-prev" @click="prevHighlightSlide">
            <img src="@/assets/img/common/content-slide-icon-left-arrow.png" alt="Previous Slide" />
          </button>
          <button class="highlight-next" @click="nextHighlightSlide">
            <img src="@/assets/img/common/content-slide-icon-right-arrow.png" alt="Next Slide" />
          </button>
          <div class="highlight-indicators">
            <img v-for="(slide, index) in highlightSlides" 
              :key="index" 
              :src="currentHighlightSlide === index ? require('@/assets/img/common/content-slide-icon-filled-circle.png') : require('@/assets/img/common/content-slide-icon-empty-circle.png')"
              @click="goToHighlightSlide(index)"
              alt="Slide Indicator"
              class="highlight-indicator-img" />
          </div>
        </div>
      </div>

      <!--    -->
      <div class="section">
        <h2> </h2>
        <!-- Calendar   -->
        <CalendarMain :events="events" />
      </div>

    </div>
  </div>
</template>

<script>
import axios from 'axios';
import CalendarMain from '../common/CalendarMain.vue';

export default {
  name: 'AppContent',
  components: {
    CalendarMain,
  },
  async mounted(){
    const token = sessionStorage.getItem('token');
    if(token!=null){
      this.isLoggedIn = true;
      this.role = sessionStorage.getItem('role');
      this.name = sessionStorage.getItem('name');
      try{
        const userData = await axios.post('/main', null, {
          params: {
              email: sessionStorage.getItem('email')
          },
          headers: {
              "Content-Type": "application/json"
          }
        })
        this.balance = userData.data.balance;
        this.membershipLevel = userData.data.membership;
      }catch(error){
        console.error("email: " + sessionStorage.getItem('email'), error);
      }
    }
    await this.fetchEvents();
  },
  data() {
    return {
      isLoggedIn: false,
      role: 'guest',
      name: null,
      balance: 0,
      membershipLevel: 'GOLD',
      //    
      goldImage: require('@/assets/img/membershipImg/gold.png'),
      //    
      silverImage: require('@/assets/img/membershipImg/silver.png'),
      //    
      bronzeImage: require('@/assets/img/membershipImg/bronze.png'),

      //   
      currentSlide: 0, //    
      slides: [
        { img: require('@/assets/img/common/content-top-slide-001.png') },
        { img: require('@/assets/img/common/content-top-slide-002.png') },
        { img: require('@/assets/img/common/content-top-slide-004.png') }
      ],
      //    
      currentHighlightSlide: 0, //      
      highlightSlides: [
        { img: require('@/assets/img/common/content-highlight-slide-009.png') },
        { img: require('@/assets/img/common/content-highlight-slide-006.png') },
        { img: require('@/assets/img/common/content-highlight-slide-002.png') }
      ],
      // CalendarMain    
      events: [],
    };
  },
  methods: {
    //   
    goToMyPage() {
      this.$router.push('/mypage/reservations');
    },
    goToRecharge(){
      this.$router.push('/myPage/info/recharge');
    },
    goToLogin(){
      this.$router.push('/login');
    },
    goToSignup(){
      this.$router.push('/signup');
    },
    goToAdminPage(){
      this.$router.push('admin/user-list');
    },
    // 
    logout() {
      //      
      sessionStorage.removeItem('token');
      sessionStorage.removeItem('name');
      sessionStorage.removeItem('email');
      sessionStorage.removeItem('role');
      const token = sessionStorage.getItem('token'); 
      if (token === null) {
        console.log('  .');
        alert(" .");
      } else {
        console.log('Content   .', token);
      }
      this.$router.push('/');
    },

    //   
    //   
    prevSlide() {
    if (this.currentSlide === 0) {
      this.currentSlide = this.slides.length - 1; //     
    } else {
      this.currentSlide--;
    }
    },
    //   
    nextSlide() {
      if (this.currentSlide === this.slides.length - 1) {
        this.currentSlide = 0; //     
      } else {
        this.currentSlide++;
      }
    },
    //   
    goToSlide(index) {
      this.currentSlide = index;
    },

    //    
    prevHighlightSlide() {
      if (this.currentHighlightSlide === 0) {
        this.currentHighlightSlide = this.highlightSlides.length - 1;
      } else {
        this.currentHighlightSlide--;
      }
    },
    nextHighlightSlide() {
      if (this.currentHighlightSlide === this.highlightSlides.length - 1) {
        this.currentHighlightSlide = 0;
      } else {
        this.currentHighlightSlide++;
      }
    },
    goToHighlightSlide(index) {
      this.currentHighlightSlide = index;
    },
    async fetchEvents() {
      try {
        const response = await axios.get('/game/schedule/main');
        this.events = response.data; 
      } catch (error) {
        console.error("Error fetching events:", error);
      }
    },
  },
};
</script>

<style scoped>
.content {
  flex-grow: 1; /*       */
  padding-top: var(--header-height);  /* Header   */
  padding-bottom: var(--footer-height);  /* Footer   */
  /* max-width: 1200px; */
  /* margin: 30px auto; */
  /*   ,   30px */
  margin: 0 auto;
  margin-bottom: 100px;
  position: relative;
}

a {
  color: #393939;
}

/*    */
.user-info-box {
  position: fixed;
  top: 595px;
  right: 22px;
  background-color: #f8f9fa;
  width: 300px;
  height: 300px;
  padding: 0px 15px;
  border: 2px solid #60a191;
  border-radius: 20px;
  text-align: right;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  justify-content: center;
  z-index: 100;
}

.btn-myPage, .btn-logout {
  background-color: #60a191;
  color: white;
  border: none;
  padding: 10px;
  margin-top: 20px;
  border-radius: 5px;
  cursor: pointer;
  width: 48%;
}

.user-info-box button:hover {
  background-color: #4f8578;
}

.user-info-box p {
  margin: 5px 20px;
}

.user-info-box a:hover {
  text-decoration: underline;
}

/*   */
.money-box {
  margin-top: 15px;
  margin-left: 10px;
  display: flex;
  align-items: center; /*   */
  gap: 1px; /*   */
  width: 250px;
  height: 80px;
  border: solid color(srgb rgb(68, 68, 68) green blue);
  border-radius: 8px;
  background: #c7dfd9;
  text-align: left;
}

.money-box p {
  margin-left: 15px; /*    */
}

.btn-charge {
  margin-left: 25px; /*    */
  width: 80px;
  background-color: #f0efc3;
  border: solid color(srgb rgb(224, 224, 224) green blue);
}

/*   */
.membership-image-container {
  display: flex;
  align-items: center; /*      */
  gap: 2px; /*     */
}

.membershipLevel-image {
  width: 50px;
  /*   */
  height: 50px;
  /*   */
  margin-left: 10px;
}

/*   */
.btn-login, .btn-signup{
  background-color: #60a191;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  width: 100%;
  height: 50px;
  margin-top: 25px;
}

/*   */
.btn-AdminPage, .btn-logout2{
  background-color: #60a191;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  width: 100%;
  height: 45px;
  margin-top: 20px;
}

.Admin-notice{
  color: #504f4f;
  font-size: 18px;
  text-align: center;
  padding-bottom: 20px;
}


/*    */
.slider-container {
  position: relative;
  width: 100%;
  height: 400px;
  margin: 0 auto;
  overflow: hidden;
}

.slides {
  display: flex;
  transition: transform 0.5s ease-in-out;
}

.slide {
  position: relative;
  min-width: 100%;
  height: 400px;
}

.slide img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.prev img,
.next img {
  width: 30px;
  height: 30px;
}

.prev, .next {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background-color: transparent;
  border: none;
  padding: 10px;
  cursor: pointer;
}

.prev {
  left: 10px;
}

.next {
  right: 10px;
}

.indicators {
  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
}

.indicator-img {
  width: 10px;
  height: 10px;
  margin: 0 5px;
  cursor: pointer;
}

/*  */
.section {
  margin-top: 50px;
  text-align: center;
}

/*     */
.highlight-slider-container {
  position: relative;
  width: 1200px;
  height: auto;
  margin: 0 auto;
  overflow: hidden;
  border-radius: 15px;
}

.highlight-slides {
  display: flex;
  transition: transform 0.5s ease-in-out;
}

.highlight-slide {
  position: relative;
  min-width: 100%;
  height: auto;
}

.highlight-slide img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 15px;
}

/*     */
.highlight-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); 
}

/*   */
.highlight-play-button {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 50px !important;
  height: 50px !important;
  z-index: 10;
  cursor: pointer;
}

.highlight-prev img,
.highlight-next img {
  width: 30px;
  height: 30px;
}

.highlight-prev, .highlight-next {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background-color: transparent;
  border: none;
  padding: 10px;
  cursor: pointer;
}

.highlight-prev {
  left: 10px;
}

.highlight-next {
  right: 10px;
}

.highlight-indicators {
  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
}

.highlight-indicator-img {
  width: 10px;
  height: 10px;
  margin: 0 5px;
  cursor: pointer;
}
</style>